import React, { Component } from "react";
import logo from "./logo.svg";
import "./App.css";
import Header from "./components/header/header";
import BX24 from "./BX24";

class App extends Component {
  componentDidMount() {
    BX24.init(function() {
      BX24.callMethod("user.current", {}, function(res) {
        var name = document.getElementById("name");
        name.innerHTML = res.data().NAME + " " + res.data().LAST_NAME;
        console.log(res.data());
      });

      console.log("B24 SDK is ready!", BX24.isAdmin());
    });

    function selectUser() {
      BX24.selectUser(data => {
        console.log(data);
      });
    }
  }

  render() {
    return (
      <>
        <Header />
        <div className="parallax" />
        <div className="someText">
          roll Up and Down this page to see the parallax scrolling effect. This
          div is just here to enable scrolling. Tip: Try to remove
        </div>
      </>
    );
  }
}

export default App;
